import * as React from "react";
const transition = "all .3s";

const inputContainerStyle = {
    transition,
    margin: "8px",
    marginTop: "2px",
    maxWidth: "80px"
};

export default function makeBinaryToggle(
    value: {isOn: boolean},
    onChange: () => void,
    displays: string[] = ["on", "off"]
) {
    const sliderContainer = {
        transition,
        position: "relative" as "relative",
        borderRadius: "12px",
        margin: "2px",
        height: "12px",
        background: value.isOn ? "#ffdddd" : "#ddffdd"
    };
    const sliderStyle = {
        transition,
        height: "12px",
        width: "12px",
        borderRadius: "50%",
        border: "1px",
        position: "absolute" as "absolute",
        background: value.isOn ? "red" : "green",
        left: value.isOn ? "0px" : "calc(100% - 13px)"
    };
    const statusTextStyle = {fontWeight: 600, textAlign: "center" as "center", fontSize: "12px"};
    return (
        <div style={inputContainerStyle} onClick={onChange}>
            <div style={sliderContainer}>
                <div style={sliderStyle} />
            </div>
            <div style={statusTextStyle}>{value.isOn ? displays[0] : displays[1]}</div>
        </div>
    );
}



