import * as React from "react";
import {observer} from "mobx-react";
import {IToolsActions, IToolbarUiTools, ToolType} from "src/toolbarUi/model/types";
import ScentOptions from "./scent/ScentOptions";
import BoidsOptions from "./boids/BoidsOptions";
import WallsOptions from "./walls/WallsOptions";

const transition = "all .3s";

const headlineStyle = {
    padding: "3px",
    paddingTop: "6px",
    paddingRight: "6px",
    textAlign: "center" as "center",
    fontWeight: 600
};

function makeExtendedOptions(props: IToolSelectorProps, selected: ToolType) {
    let r = null;
    switch (selected) {
        case ToolType.walls:
            r = <WallsOptions {...{props, selected}} />;
            break;
        case ToolType.scent:
            r = <ScentOptions {...{...props, selected}} />;
            break;
        case ToolType.boids:
            r = <BoidsOptions {...{...props, selected}} />;
            break;
        default:
            break;
    }

    return r;
}

function makeToolEntry(props: IToolSelectorProps, a: ToolType, isFirst: boolean, isLast: boolean) {
    const onClick = () => props.actions.select(a);
    const isSelected = props.tools.selected === a;
    const br = () => {
        if (isFirst) {
            return isLast ? "6px 6px 6px 6px" : "6px 6px 0px 0px";
        }
        if (isLast) {
            return isSelected ? "0px 0px 0px 0px" : "0px 0px 6px 6px";
        }
        return "0px 0px 0px 0px";
    };

    return (
        <div
            key={`tool_${a}`}
            style={{
                borderRadius: "6px 6px 6px 6px",
                paddingTop: "3px",
                paddingBottom: isLast ? "3px" : "0px",
                userSelect: "none",
                display: "flex",
                flexDirection: "column"
            }}>
            <button
                onClick={onClick}
                style={{
                    transition,
                    fontSize: "18px",
                    outline: "none",
                    borderRadius: br(),
                    width: "100%",
                    background: isSelected ? "#94b0b7" : "white",
                    userSelect: "none"
                }}>
                {a}
            </button>
            {makeExtendedOptions(props, a)}
        </div>
    );
}

export interface IToolSelectorProps {
    tools: IToolbarUiTools;
    actions: IToolsActions;
}

export default observer(
    class ToolSelector extends React.Component<IToolSelectorProps> {
        public render() {
            return (
                <div
                    style={{
                        borderRadius: "6px",
                        margin: "3px",
                        padding: "3px",
                        paddingRight: "6px",
                        display: "flex",
                        flexDirection: "column",
                        background: "#ddddda",
                        border: "1px solid #7697a0",
                        userSelect: "none"
                    }}>
                    <div style={headlineStyle}>Tools</div>
                    {this.props.tools.active.map((a, i) =>
                        makeToolEntry(this.props, a, !i, i === this.props.tools.active.length - 1)
                    )}
                </div>
            );
        }
    }
);



