import * as React from "react";

const transition = "all .3s";

const inputContainerStyle = {
    transition,
    margin: "8px",
    marginTop: "2px",
    maxWidth: "80px"
};

const entryTitleStyle = {fontWeight: 600, textAlign: "center" as "center", fontSize: "12px"};

const inputStyle = {width: "100%", padding: "0"};

export const makeSliderInput = (
    value: number,
    min: number,
    max: number,
    step: number,
    title: string,
    onChange: (e: React.ChangeEvent<HTMLInputElement>) => void,
    processValue?: (value: number) => number
) => {
    return (
        <div style={inputContainerStyle}>
            <div style={entryTitleStyle}>{title}</div>
            <div style={{position: "relative", display: "flex", flexDirection: "column", justifyContent: "center"}}>
                <div
                    style={{
                        fontWeight: "bolder",
                        position: "absolute",
                        width: "100%",
                        textAlign: "center",
                        display: "flex",
                        justifyContent: "center",
                        pointerEvents: "none"
                    }}>
                    <div>{processValue ? processValue(value) : value}</div>
                </div>
                <input
                    style={inputStyle}
                    type="range"
                    name={title}
                    min={min}
                    max={max}
                    step={step}
                    value={processValue ? processValue(value) : value}
                    onChange={onChange}
                />
            </div>
        </div>
    );
};



