import * as React from "react";
import {observer} from "mobx-react";
import {IToolbarUiPerformance} from "src/toolbarUi/model/types";

export default observer(
    class PerformanceDisplay extends React.Component<IToolbarUiPerformance> {
        public render() {
            return (
                <div style={{padding: "3px", paddingBottom: "0"}}>
                    <div
                        style={{
                            borderRadius: "6px",
                            padding: "3px",
                            height: "fit-content",
                            background: "#ddddda",
                            border: "1px solid #7697a0",
                            pointerEvents: "none",
                            userSelect: "none",
                            textAlign: "center" as "center"
                        }}>
                        <span style={{fontWeight: 600}}>{`${this.props.fps} `}</span>
                        <span style={{fontSize: "11px"}}>FPS </span>
                    </div>
                </div>
            );
        }
    }
);



