import Grid from "src/scentmap/hexgrid/Grid";
import {ToolType, IToolbarUiState} from "src/toolbarUi/model/types";
import {IScent} from "src/scentmap/scent/types";
import changeScent from "src/scentmap/scent/changeScent";
import changeWall from "src/scentmap/walls/changeWall";
import makeToolSelectionReaction from "./makeToolSelectionReaction";
import makeScentToolReaction from "./makeScentToolReaction";
import {IBoid, makeBoid} from "src/boids/makeBoids";
import makeBoidsToolReaction from "./makeBoidsToolReaction";

export enum ToolAction {
    none = "none",
    addWall = "addWall",
    eraseWall = "eraseWall",
    addBoid = "addBoid",
    removeBoid = "removeBoid",
    addScent = "addScent"
}
export interface IToolActions {
    actions: {
        none: (node: number) => void;
        addWall: (node: number) => void;
        eraseWall: (node: number) => void;
        addBoid: (node: number) => void;
        removeBoid: (node: number) => void;
        addScent: (node: number) => void;
    };
    selected: ToolAction;
    actOnNode: (node: number) => void;
}

export default function makeToolActions(
    appState: IToolbarUiState,
    walls: number[],
    scent: IScent,
    boids: {boids: IBoid[]},
    grid: Grid
): IToolActions {
    const toolActions = {
        actions: {
            none: (node: number) => {
                return;
            },
            addWall: (node: number) => changeWall(walls, node, 0),
            eraseWall: (node: number) => changeWall(walls, node, 1),
            addBoid: (node: number) => {
                boids.boids.push(
                    makeBoid({...appState.data.tools.boids, node, position: {...grid.gridArr[node].position}})
                );
            },
            removeBoid: (node: number) => {
                const b = boids.boids.pop() as any;
                if (!b || !b.onDelete) return;
                b.onDelete();
            },
            addScent: (node: number) => changeScent(scent, node)
        },
        actOnNode: (node: number) => {
            const a = toolActions.actions[toolActions.selected];
            a && a(node);
        },
        selected: ToolAction.none
    };
    const setActionByTool = (tool: ToolType) => {
        switch (tool) {
            case ToolType.none:
                toolActions.selected = ToolAction.none;
                break;
            case ToolType.walls:
                toolActions.selected = appState.data.tools.walls.isErasing ? ToolAction.eraseWall : ToolAction.addWall;
                break;
            case ToolType.scent:
                toolActions.selected = ToolAction.addScent;
                break;
            case ToolType.boids:
                toolActions.selected = appState.data.tools.boids.isRemoving
                    ? ToolAction.removeBoid
                    : ToolAction.addBoid;
                break;
            default:
                break;
        }
    };
    makeToolSelectionReaction(appState, setActionByTool);
    makeScentToolReaction(appState, scent);
    makeBoidsToolReaction(appState, boids);
    return toolActions;
}



