import {action} from "mobx";
import {IToolbarUiData, ToolType} from "src/toolbarUi/model/types";

export const getUpdateSelected = (appData: IToolbarUiData) =>
    action((selected: string) => {
        appData.tools.selected = ToolType[selected];
    });



