import Grid from "src/scentmap/hexgrid/Grid";
import {IScent} from "./types";

export function makeWeightedDistributionUpdate(grid: Grid, walls: number[], scent: IScent, numNeighbors: number = 6) {
    const deltas = new Array(walls.length).fill(0);
    return (delta: number) => {
        const {scentGrid, evaporation, spread} = scent;
        const sg = [...scentGrid];
        for (let self = 0; self < scentGrid.length; self++) {
            const p = scentGrid[self];
            const w = walls[self];
            if (isNaN(p)) {
                sg[self] = 0;
                continue;
            }
            if (p <= 0 || !w) continue;

            const ns = grid.gridArr[self].neighbors;
            let dSum = 0;
            let avSum = p;

            for (const neighbor of ns) {
                const s = scentGrid[neighbor] < p && walls[neighbor] ? scentGrid[neighbor] : p;
                const v = s < p ? (p - s) * (w * walls[neighbor]) : 0;
                avSum += s;
                dSum += v;
                deltas[neighbor] = v;
            }
            if (!dSum) continue;

            avSum /= numNeighbors + 1;
            dSum = Math.min(dSum, (p - avSum) * (spread * delta));
            const sum = Math.min(p - avSum, dSum);

            for (const neighbor of ns) {
                const r = deltas[neighbor] / dSum;
                sg[neighbor] = sg[neighbor] + sum * r;
            }

            sg[self] = Math.max(0, (sg[self] - sum) * evaporation);
        }
        scent.scentGrid = sg;
    };
}

export default function makeEqualDistributeUpdate(
    grid: Grid,
    walls: number[],
    scent: IScent,
    numNeighbors: number = 6
) {
    return (delta: number) => {
        const {scentGrid, evaporation, spread, minValue} = scent;
        const sg = [...scentGrid];
        for (let self = 0; self < scentGrid.length; self++) {
            const p = scentGrid[self];
            const w = walls[self];
            if (isNaN(p) || !w) {
                sg[self] = 0;
                continue;
            }
            const targets = grid.gridArr[self].neighbors;
            const dSpread = Math.min(1, spread * delta);
            const distribute = p * dSpread;
            const dSlice = distribute / numNeighbors;
            let remains = distribute;
            for (const id of targets) {
                const v = isNaN(walls[id]) ? 0 : dSlice * walls[id];
                remains -= v;
                sg[id] += v;
            }
            sg[self] = sg[self] - (distribute - remains);
            sg[self] *= evaporation;
        }

        scent.scentGrid = sg.map(v => (v > minValue ? v : 0));
    };
}



