import {
    addVector,
    subtractVector,
    normalize,
    multiplyScalar,
    clamp,
    divideByScalar,
    distanceSq,
    magnitude
} from "./../utils/geom";
import {IPoint} from "src/utils/geom";

export interface IBoid {
    position: IPoint;
    velocity: IPoint;
    acceleration: IPoint;
    drag: number;
    speed: number;
    maxForce: number;
    radius: number;
}

export function makeBoid(params?: any) {
    return {
        ...{
            position: {x: 0, y: 0},
            velocity: {x: 0, y: 0},
            acceleration: {x: 0, y: 0},
            drag: 0.1,
            speed: 1,
            maxForce: 10,
            radius: 5
        },
        ...params
    };
}

export function makeBoids(numBoids: number, template?: any) {
    return Array.from({length: numBoids}, n => makeBoid(template));
}

export function applyForce(acceleration: IPoint, force: IPoint) {
    addVector(acceleration, force);
}

export function seek(target: IPoint, boid: IBoid) {
    const d = subtractVector({...target}, boid.position);
    normalize(d);
    multiplyScalar(d, boid.speed);
    subtractVector(d, boid.velocity);
    clamp(d, boid.maxForce);
    return d;
}

export function avoid(target: IPoint, boid: IBoid) {
    const d = subtractVector({...target}, boid.position);
    normalize(d);
    multiplyScalar(d, -boid.speed);
    subtractVector(d, boid.velocity);
    clamp(d, boid.maxForce);
    return d;
}

export function cohesion(boid: IBoid, neighbors: IPoint[]) {
    const steer = {x: 0, y: 0};
    const numNeighbors = neighbors.length;
    if (!numNeighbors) return {x: 0, y: 0};
    for (let neighbor of neighbors) {
        addVector(steer, neighbor);
    }
    return seek(divideByScalar(steer, numNeighbors), boid);
}

export function separation(boid: IBoid, neighbors: IPoint[]) {
    const steer = {x: 0, y: 0};
    const numNeighbors = neighbors.length;
    if (!numNeighbors) return {x: 0, y: 0};
    for (let neighbor of neighbors) {
        const d = distanceSq(boid.position, neighbor) || Math.random();
        const diff = subtractVector({...boid.position}, neighbor);
        normalize(diff);
        divideByScalar(diff, d);
        addVector(steer, diff);
    }
    divideByScalar(steer, numNeighbors);
    const steerForce = magnitude(steer);

    if (steerForce > 0) {
        normalize(steer);
        multiplyScalar(steer, boid.speed);
        subtractVector(steer, boid.velocity);
        clamp(steer, boid.maxForce);
    }
    return steer;
}

export function alignment(boid: IBoid, neighbors: IBoid[]) {
    const steer = {x: 0, y: 0};
    const numNeighbors = neighbors.length;
    if (!numNeighbors) return {x: 0, y: 0};
    for (let neighbor of neighbors) {
        addVector(steer, neighbor.velocity);
    }
    divideByScalar(steer, numNeighbors);
    normalize(steer);
    multiplyScalar(steer, boid.speed);
    subtractVector(steer, boid.velocity);
    clamp(steer, boid.maxForce);
    return steer;
}

export function updateBoid(boid: IBoid, forces: IPoint[]) {
    for (let force of forces) {
        applyForce(boid.acceleration, force);
    }

    addVector(boid.velocity, boid.acceleration);
    clamp(boid.velocity, boid.speed);
    addVector(boid.position, boid.velocity);
    multiplyScalar(boid.acceleration, 0);
    multiplyScalar(boid.velocity, boid.drag);
}



