import Grid from "src/scentmap/hexgrid/Grid";
import {IScent} from "./types";
import {uuid} from "src/utils/uuid";

export function makeScent(
    grid: Grid,
    spread: number = 0,
    evaporation: number = 1000,
    quantity: number = 1000,
    hue: number = 0xff0000,
    minValue: number = 0.01
): IScent {
    const scentGrid: number[] = Array.from(grid.gridArr, n => 0);
    const permeability: number[] = Array.from(grid.gridArr, n => 1);
    return {
        scentGrid,
        permeability,
        id: uuid(),
        spread: spread || 1,
        evaporation: 1 - Math.min(1, 1 / evaporation),
        quantity,
        hue,
        minValue
    };
}



