import Grid from "../../scentmap/hexgrid/Grid";
import {IToolbarUiState} from "../../toolbarUi/model/types";
import addDisplayClickListener from "../addDisplayClickListener";
import makeToolActions from "./makeToolActions";
import addDisplayDragListener from "../addDisplayDragListener";
import {IDisplay} from "../../pixi/types";
import {IScent} from "../../scentmap/scent/types";
import {IBoid} from "../../boids/makeBoids";

export function addToolActions(
    appState: IToolbarUiState,
    display: IDisplay,
    grid: Grid,
    walls: number[],
    scent: IScent,
    boids: {boids: IBoid[]}
) {
    const toolActions = makeToolActions(appState, walls, scent, boids, grid);
    addDisplayClickListener(display, grid, toolActions.actOnNode);
    const onStart: (node: number) => void = (node: number) => {
        toolActions.selected !== "none" && display.camera.pausePlugin("drag");
    };
    const onEnd: (node: number) => void = (node: number) => {
        toolActions.selected !== "none" && display.camera.resumePlugin("drag");
    };
    const onMove: (node: number) => void = toolActions.actOnNode;
    addDisplayDragListener(display, grid, onStart, onEnd, onMove);
}



