import * as React from "react";
import {hexValueToString} from "src/utils/colorConversions";

const transition = "all .3s";

const inputContainerStyle = {
    transition,
    margin: "8px",
    marginTop: "2px",
    maxWidth: "80px"
};

const entryTitleStyle = {fontWeight: 600, textAlign: "center" as "center", fontSize: "12px"};

const inputStyle = {width: "100%", padding: "0"};
export default function makeHueSelector(hue: number, onChange: (e: React.ChangeEvent<HTMLInputElement>) => void) {
    return (
        <div style={inputContainerStyle}>
            <div style={entryTitleStyle}>Hue</div>
            <input style={inputStyle} type="color" name="hue" value={hexValueToString(hue)} onChange={onChange} />
        </div>
    );
}



