import {IToolbarUiState} from "src/toolbarUi/model/types";
import {autorun} from "mobx";
import {IScent} from "src/scentmap/scent/types";

export default function makeScentToolReaction(appState: IToolbarUiState, scent: IScent) {
    autorun(() => {
        scent.evaporation = 1 - Math.min(1, appState.data.tools.scent.evaporate);
        scent.spread = appState.data.tools.scent.spread;
        scent.quantity = appState.data.tools.scent.quantity;
        scent.hue = appState.data.tools.scent.hue;
    });
}



