import * as React from "react";
import makeDisplay, {BgType} from "./pixi/makeDisplay";
import Grid from "./scentmap/hexgrid/Grid";
import {makeWalls} from "./scentmap/walls/makeWalls";
import {makeScent} from "./scentmap/scent/makeScent";
import {makeUpdateHexColorsByScent} from "./pixi/scent/makeUpdateHexColorsByScent";
import {IToolbarUiState} from "./toolbarUi/model/types";
import makeToolbarUiState from "./toolbarUi/model/makeToolbarUiState";
import ComponentsScaffold from "./toolbarUi/components/ComponentsScaffold";
import timeVariableLoop, {ITime} from "./utils/loop/timeVariableLoop";
import {updateFPS} from "./toolbarUi/actions/updateFPS";
import {makeHexesGrid} from "./pixi/hexgrid/makeHexesGrid";
import makeScentUpdate from "./scentmap/scent/makeUpdate";
import {updateWallsView} from "./pixi/walls/updateWallsView";
import {IDisplay} from "./pixi/types";
import {IScent} from "./scentmap/scent/types";
import {makeGridDisplayBoids} from "./interactions/boids/makeGridDisplayBoids";
import {addToolActions} from "./interactions/tools/addToolActions";

/*
    Fix
        boids passing through holes where nearby walls are too scary
        consider time delta when calculating boids update
        scents sticking to walls
    Automate UI generation
        takes a mobx state and a settings object
        returns a React component
    Multiple scents
        good scent
        bad scent
        scent droppers 
            dispense X smell over Y time
            then disappear
    Multiple boid types
    Screen Tool
        save/load walls
        set size
    Backgrounds Tool
        choose background
*/

const rows = 100;
const columns = rows * 2;
const hexSize = 10;
const spread = 1000;
export const maxBoids = 2000;
export const statingBoidsNum = 0;

function addUpdates(
    uiState: IToolbarUiState,
    display: IDisplay,
    scentSprites: PIXI.Sprite[],
    grid: Grid,
    walls: number[],
    scent: IScent,
    boidsUpdate: (delta?: number) => void
) {
    const time = {speed: 1} as ITime;
    const updateScents = makeScentUpdate(grid, walls, scent);
    const updateScentsViews = makeUpdateHexColorsByScent(scentSprites, scent);

    const update = (delta: number) => {
        updateFPS(uiState, delta);
        updateScents(delta);
        updateScentsViews(delta);
        updateWallsView(walls, display.wallsSprites);
        boidsUpdate(delta);
    };
    timeVariableLoop(time, update);
}

function initialize(gameCanvas: HTMLDivElement, uiState: IToolbarUiState) {
    const grid = new Grid({rows, columns, hexSize});
    const walls = makeWalls(grid);
    const display = makeDisplay(gameCanvas, grid, walls, BgType.none);

    const scent = makeScent(grid, spread);
    const scentDisplay = makeHexesGrid(grid, display.hexTexture, scent.hue);
    display.camera.addChild(scentDisplay.view);

    const boids = makeGridDisplayBoids(display, uiState.data.tools.boids, grid, walls, scent);

    addUpdates(uiState, display, scentDisplay.nodes, grid, walls, scent, boids.update);
    addToolActions(uiState, display, grid, walls, scent, boids);
}

class App extends React.Component {
    private _gameCanvas: HTMLDivElement;
    private _appState: IToolbarUiState;

    constructor(p: any) {
        super(p);
        this._appState = makeToolbarUiState();
    }
    public componentDidMount() {
        this._gameCanvas && initialize(this._gameCanvas, this._appState);
    }

    public render() {
        return (
            <div
                ref={thisDiv => {
                    thisDiv && (this._gameCanvas = thisDiv);
                }}>
                <ComponentsScaffold {...this._appState} />
            </div>
        );
    }
}

export default App;



