export function makeBoidsViews(tex: PIXI.Texture, quantity: number, tint: number = 0xaa0000) {
    const view = new PIXI.particles.ParticleContainer(1000, {
        scale: true,
        alpha: true
    });
    const sprites = Array.from({length: quantity}, () => {
        const sprite = new PIXI.Sprite(tex);
        view.addChild(sprite);
        sprite.tint = tint;
        sprite.anchor.set(0.5);
        return sprite;
    });

    return {view, sprites};
}



