import Grid from "../../scentmap/hexgrid/Grid";
import {IScent} from "../../scentmap/scent/types";
import {makeBoids, updateBoid, IBoid, separation, cohesion, alignment} from "../../boids/makeBoids";
import {IPoint, multiplyScalar} from "../../utils/geom";
import {statingBoidsNum} from "../../App";

export type IGridBoid = IBoid & {
    node: number;
    neighbors: IGridBoid[];
    hue: number;
    olfaction: number;
    onDelete: () => void;
};

export interface IBoidTemplate {
    drag: number;
    speed: number;
    maxForce: number;
    radius: number;
    hue: number;
    olfaction: number;
    weights: {
        separation: number;
        cohesion: number;
        seekScent: number;
        avoidObstacles: number;
        alignment: number;
    };
}

export function makeGridBoids(boidTemplate: IBoidTemplate, grid: Grid, walls: number[], scent: IScent) {
    const boids = (makeBoids(statingBoidsNum, boidTemplate) as any) as IGridBoid[];
    const boidNodes = Array.from(grid.gridArr, () => new Set<IGridBoid>());
    const numNodes = boidNodes.length;
    const update = (delta?: number) => {
        boids.forEach(boid => {
            if (boid.node < 0 || boid.node >= numNodes) return;
            boidNodes[boid.node || 0].delete(boid);
            boid.onDelete =
                boid.onDelete ||
                (() => {
                    boidNodes[boid.node || 0].delete(boid);
                });
            const node = grid.getHexIdByPixel(boid.position);
            if (isNaN(node)) return;
            boid.node = node;
            boidNodes[boid.node].add(boid);
            const neighborNodes = grid.gridArr[boid.node].neighbors.filter(n => !isNaN(n));
            const neighbors = new Array<IGridBoid>();
            const obstacles = new Array<IPoint>();
            neighborNodes.forEach(n => {
                Array.from(boidNodes[n].values()).forEach(n2 => neighbors.push(n2));
                !walls[n] && obstacles.push(grid.gridArr[n].position);
            });
            let highestScent: number = 0;
            let goal: IPoint | null = null;
            for (let neighbor of neighborNodes) {
                const val = scent.scentGrid[neighbor];
                if (val > highestScent && val > boid.olfaction) {
                    highestScent = val;
                    goal = {...grid.gridArr[neighbor].position};
                }
            }
            const goals = goal ? [goal] : [];

            const neighborPositions = neighbors.map(n => n.position);
            const forces = [
                multiplyScalar(separation(boid, obstacles), boidTemplate.weights.avoidObstacles),
                multiplyScalar(cohesion(boid, goals), boidTemplate.weights.seekScent),
                multiplyScalar(cohesion(boid, neighborPositions), boidTemplate.weights.cohesion),
                multiplyScalar(separation(boid, neighborPositions), boidTemplate.weights.separation),
                multiplyScalar(alignment(boid, neighbors), boidTemplate.weights.alignment)
            ];

            updateBoid(boid, forces);
        });
    };
    return {boids, update};
}



