import {IToolbarUiState} from "../model/types";
import {action} from "mobx";

const stackSize = 60;
const fpsStack: number[] = new Array(stackSize).fill(0);
let sum = 0;

export const updateFPS = action((app: IToolbarUiState, delta: number) => {
    sum -= fpsStack.shift() || 0;
    const nfps = 1 / delta;
    fpsStack.push(nfps);
    sum += nfps;
    app.data.performance.fps = Math.floor(sum / stackSize);
});



