import * as React from "react";
import {IToolSelectorProps} from "../ToolSelector";
import {observer} from "mobx-react";
import {hexStringToRgb, rgbArrayToHex, hexValueToString} from "src/utils/colorConversions";
import {ToolType} from "src/toolbarUi/model/types";
import {makeSliderInput} from "../../utils/makeSlider";
import makeHueSelector from "../../utils/makeHueSelector";
import makeBinaryToggle from "../../utils/makeBinaryToggle";

const transition = "all .3s";

export default observer(
    class BoidsOptions extends React.Component<IToolSelectorProps & {selected: ToolType}> {
        public render() {
            const containerStyle = {
                transition,
                borderRadius: "0 0 6px 6px",
                maxHeight: this.props.tools.selected === this.props.selected ? "700px" : "0px", // ugly, find a better solution
                overflow: "hidden",
                background: "white",
                width: "100%",
                display: "flex",
                flexDirection: "column" as "column"
            };
            const props = this.props;
            const boidsTool = props.tools.boids;
            const onDragChanged = (e: React.ChangeEvent<HTMLInputElement>) => {
                props.actions.boids.changeDrag(parseFloat(e.target.value));
            };
            const onOlfactionChanged = (e: React.ChangeEvent<HTMLInputElement>) => {
                props.actions.boids.changeOlfaction(parseFloat(e.target.value));
            };
            const onSpeedChanged = (e: React.ChangeEvent<HTMLInputElement>) => {
                props.actions.boids.changeSpeed(parseInt(e.target.value, 10));
            };
            const onMaxForceChanged = (e: React.ChangeEvent<HTMLInputElement>) => {
                props.actions.boids.changeMaxForce(parseInt(e.target.value, 10));
            };
            const onRadiusChanged = (e: React.ChangeEvent<HTMLInputElement>) => {
                props.actions.boids.changeRadius(parseFloat(e.target.value));
            };
            const onHueChanged = (e: React.ChangeEvent<HTMLInputElement>) => {
                const v = e.target.value.slice(1);
                const rgb = hexStringToRgb(v);
                const hex = rgbArrayToHex(rgb);
                props.actions.boids.changeHue(hex);
            };
            const onSeparationWeightChanged = (e: React.ChangeEvent<HTMLInputElement>) => {
                props.actions.boids.changeSeparationWeight(parseFloat(e.target.value));
            };
            const onCohesionWeightChanged = (e: React.ChangeEvent<HTMLInputElement>) => {
                props.actions.boids.changeCohesionWeight(parseFloat(e.target.value));
            };
            const onSeekScentWeightChanged = (e: React.ChangeEvent<HTMLInputElement>) => {
                props.actions.boids.changeSeekScentWeight(parseFloat(e.target.value));
            };
            const onAvoidObstaclesWeightChanged = (e: React.ChangeEvent<HTMLInputElement>) => {
                props.actions.boids.changeAvoidObstacleScentWeight(parseFloat(e.target.value));
            };
            const onAlignmentWeightChanged = (e: React.ChangeEvent<HTMLInputElement>) => {
                props.actions.boids.changeAlignmentWeight(parseFloat(e.target.value));
            };
            const onToggleIsRemoving = this.props.actions.boids.toggleIsRemoving;

            const separatorTitleStyle = {
                fontWeight: 600,
                textAlign: "center" as "center",
                fontSize: "12px",
                width: "100%",
                padding: "0"
            };
            return (
                <div style={containerStyle}>
                    {makeBinaryToggle({isOn: this.props.tools.boids.isRemoving}, onToggleIsRemoving, [
                        "Remove",
                        "Place"
                    ])}
                    {makeSliderInput(boidsTool.drag, 0.0, 1.0, 0.1, "drag", onDragChanged)}
                    {makeSliderInput(boidsTool.speed, 0, 10, 0.1, "speed", onSpeedChanged)}
                    {makeSliderInput(boidsTool.maxForce, 0, 10, 0.1, "max force", onMaxForceChanged)}
                    {makeSliderInput(boidsTool.radius, 0, 3, 0.1, "radius", onRadiusChanged)}
                    {makeSliderInput(boidsTool.olfaction, 0, 10, 0.1, "olfaction", onOlfactionChanged)}
                    <div style={{textAlign: "center", paddingTop: "3px"}}>***********</div>
                    <div style={{...separatorTitleStyle, paddingBottom: "3px"}}>behavior weights</div>
                    {makeSliderInput(boidsTool.weights.separation, 0, 10, 0.1, "separation", onSeparationWeightChanged)}
                    {makeSliderInput(boidsTool.weights.cohesion, 0, 10, 0.1, "cohesion", onCohesionWeightChanged)}
                    {makeSliderInput(boidsTool.weights.seekScent, 0, 10, 0.1, "seek scent", onSeekScentWeightChanged)}
                    {makeSliderInput(
                        boidsTool.weights.avoidObstacles,
                        0,
                        10,
                        0.1,
                        "avoid obstacles",
                        onAvoidObstaclesWeightChanged
                    )}
                    {makeSliderInput(boidsTool.weights.alignment, 0, 10, 0.1, "alignment", onAlignmentWeightChanged)}
                    <div style={{textAlign: "center"}}>***********</div>
                    {makeHueSelector(boidsTool.hue, onHueChanged)}
                </div>
            );
        }
    }
);



