import {IToolbarUiState} from "src/toolbarUi/model/types";
import {autorun} from "mobx";
import {IBoid} from "src/boids/makeBoids";

export default function makeBoidsToolReaction(appState: IToolbarUiState, boids: {boids: IBoid[]}) {
    autorun(() => {
        const speed = appState.data.tools.boids.speed;
        const drag = appState.data.tools.boids.drag;
        const radius = appState.data.tools.boids.radius;
        const maxForce = appState.data.tools.boids.maxForce;
        boids.boids.forEach(b => {
            b.speed = speed;
            b.drag = drag;
            b.radius = radius;
            b.maxForce = maxForce;
        });
    });
}



