import * as React from "react";
import {observer} from "mobx-react";
import {IToolbarUiState} from "../model/types";
import PerformanceDisplay from "./performance/PerformanceDisplay";
import ToolSelector from "./tools/ToolSelector";

export default observer(
    class ComponentsScaffold extends React.Component<IToolbarUiState> {
        public render() {
            return (
                <div
                    style={{
                        borderRadius: "6px 6px 6px 6px",
                        padding: "1px",
                        position: "fixed",
                        top: "0px",
                        width: "fit-content",
                        left: "0px",
                        display: "flex",
                        background: "white",
                        flexDirection: "column"
                    }}>
                    <PerformanceDisplay {...this.props.data.performance} />
                    <ToolSelector
                        {...{
                            tools: this.props.data.tools,
                            actions: this.props.actions.tools
                        }}
                    />
                </div>
            );
        }
    }
);



