import {IToolbarUiState, ToolType} from "src/toolbarUi/model/types";
import {autorun} from "mobx";

export default function makeToolSelectionReaction(appState: IToolbarUiState, onSelection: (tool: ToolType) => void) {
    autorun(() => onSelection(appState.data.tools.selected));
}



