import {IAction} from "mobx";

export interface IToolbarUiPerformance {
    fps: number;
    visible: boolean;
}

export interface IWallsTool {
    visible: boolean;
    isErasing: boolean;
}

export interface IScentTool {
    visible: boolean;
    quantity: number;
    spread: number;
    evaporate: number;
    hue: number;
}

export interface IBoidsTool {
    visible: boolean;
    isRemoving: boolean;
    quantity: number;
    drag: number;
    speed: number;
    maxForce: number;
    radius: number;
    olfaction: number;
    hue: number;
    weights: {
        separation: number;
        cohesion: number;
        seekScent: number;
        avoidObstacles: number;
        alignment: number;
    };
}

export interface IToolbarUiTools {
    visible: boolean;
    selected: ToolType;
    active: ToolType[];
    walls: IWallsTool;
    scent: IScentTool;
    boids: IBoidsTool;
}

export enum ToolType {
    walls = "walls",
    scent = "scent",
    boids = "boids",
    none = "none"
}

export interface IToolbarUiData {
    performance: IToolbarUiPerformance;
    tools: IToolbarUiTools;
}

export interface IWallsActions {
    toggleIsErasing: (() => void) & IAction;
}

export interface IScentActions {
    changeEvaporate: ((value: number) => void) & IAction;
    changeSpread: ((value: number) => void) & IAction;
    changeQuantity: ((value: number) => void) & IAction;
    changeHue: ((value: number) => void) & IAction;
}

export interface IBoidsActions {
    toggleIsRemoving: (() => void) & IAction;
    changeQuantity: ((value: number) => void) & IAction;
    changeHue: ((value: number) => void) & IAction;
    changeOlfaction: ((value: number) => void) & IAction;
    changeDrag: ((value: number) => void) & IAction;
    changeSpeed: ((value: number) => void) & IAction;
    changeMaxForce: ((value: number) => void) & IAction;
    changeRadius: ((value: number) => void) & IAction;
    changeSeparationWeight: ((value: number) => void) & IAction;
    changeCohesionWeight: ((value: number) => void) & IAction;
    changeSeekScentWeight: ((value: number) => void) & IAction;
    changeAvoidObstacleScentWeight: ((value: number) => void) & IAction;
    changeAlignmentWeight: ((value: number) => void) & IAction;
}

export interface IToolsActions {
    select: ((selected: string) => void) & IAction;
    walls: IWallsActions;
    scent: IScentActions;
    boids: IBoidsActions;
}

export interface IToolbarUiActions {
    tools: IToolsActions;
}

export interface IToolbarUiState {
    data: IToolbarUiData;
    actions: IToolbarUiActions;
}



