import {makeToggleBoolean} from "./../actions/makeToggleBoolean";
import {makeChangeValue} from "../actions/makeChangeValue";
import {observable} from "mobx";
import {IToolbarUiState, ToolType, IToolbarUiData, IToolbarUiActions} from "./types";
import {getUpdateSelected} from "../actions/tools/getUpdateSelected";
export default function makeToolbarUiState(): IToolbarUiState {
    const data: IToolbarUiData = observable({
        performance: {fps: 0, visible: true},
        tools: {
            visible: true,
            selected: ToolType.none,
            active: [ToolType.none, ToolType.scent, ToolType.boids, ToolType.walls],
            walls: {
                visible: true,
                isErasing: false
            },
            scent: {
                visible: true,
                quantity: 100,
                spread: 10,
                evaporate: 0.1,
                hue: 0x80ffff
            },
            boids: {
                visible: true,
                isRemoving: false,
                quantity: 1500,
                drag: 0.1,
                speed: 1,
                maxForce: 1,
                radius: 1,
                olfaction: 0.1,
                hue: 0xaa0000,
                weights: {
                    separation: 1,
                    cohesion: 0.5,
                    seekScent: 0.5,
                    avoidObstacles: 10,
                    alignment: 1
                }
            }
        }
    });

    const actions: IToolbarUiActions = {
        tools: {
            select: getUpdateSelected(data),
            walls: {
                toggleIsErasing: makeToggleBoolean(data.tools.walls, "isErasing")
            },
            scent: {
                changeQuantity: makeChangeValue(data.tools.scent, "quantity"),
                changeHue: makeChangeValue(data.tools.scent, "hue"),
                changeEvaporate: makeChangeValue(data.tools.scent, "evaporate"),
                changeSpread: makeChangeValue(data.tools.scent, "spread")
            },
            boids: {
                toggleIsRemoving: makeToggleBoolean(data.tools.boids, "isRemoving"),
                changeQuantity: makeChangeValue(data.tools.boids, "quantity"),
                changeOlfaction: makeChangeValue(data.tools.boids, "olfaction"),
                changeHue: makeChangeValue(data.tools.boids, "hue"),
                changeDrag: makeChangeValue(data.tools.boids, "drag"),
                changeSpeed: makeChangeValue(data.tools.boids, "speed"),
                changeMaxForce: makeChangeValue(data.tools.boids, "maxForce"),
                changeRadius: makeChangeValue(data.tools.boids, "radius"),
                changeSeparationWeight: makeChangeValue(data.tools.boids.weights, "separation"),
                changeCohesionWeight: makeChangeValue(data.tools.boids.weights, "cohesion"),
                changeSeekScentWeight: makeChangeValue(data.tools.boids.weights, "seekScent"),
                changeAvoidObstacleScentWeight: makeChangeValue(data.tools.boids.weights, "avoidObstacles"),
                changeAlignmentWeight: makeChangeValue(data.tools.boids.weights, "alignment")
            }
        }
    };

    return {data, actions};
}



