import * as React from "react";
import {IToolSelectorProps} from "../ToolSelector";
import {ToolType} from "src/toolbarUi/model/types";
import makeBinaryToggle from "../../utils/makeBinaryToggle";
import {observer} from "mobx-react";

const transition = "all .3s";

export default observer(
    class WallsOptions extends React.Component<{props: IToolSelectorProps; selected: ToolType}> {
        public render() {
            const containerStyle = {
                transition,
                borderRadius: "0 0 6px 6px",
                maxHeight: this.props.props.tools.selected === this.props.selected ? "50px" : "0px", // ugly, find a better solution
                overflow: "hidden",
                background: "white",
                width: "100%",
                display: "flex",
                flexDirection: "column" as "column"
            };

            const onClick = this.props.props.actions.walls.toggleIsErasing;
            return (
                <div style={containerStyle}>
                    {makeBinaryToggle({isOn: this.props.props.tools.walls.isErasing}, onClick, ["Remove", "Place"])}
                </div>
            );
        }
    }
);



