import Grid from "../../scentmap/hexgrid/Grid";
import {IDisplay} from "../../pixi/types";
import {IScent} from "../../scentmap/scent/types";
import {makeBoidsViews} from "../../pixi/boids/makeBoidsViews";
import {makeGridBoids, IBoidTemplate} from "./makeGridBoids";
import {maxBoids} from "../../App";

export function makeGridDisplayBoids(
    display: IDisplay,
    boidTemplate: IBoidTemplate,
    grid: Grid,
    walls: number[],
    scent: IScent
) {
    const gridBoids = makeGridBoids(boidTemplate, grid, walls, scent);
    gridBoids.boids.forEach(boid => {
        boid.position.x = display.camera.worldWidth / 2 + (150 - Math.random() * 300);
        boid.position.y = display.camera.worldHeight / 2 + (150 - Math.random() * 300);
    });
    const boidViews = makeBoidsViews(display.hexTexture, maxBoids);
    const update = (delta?: number) => {
        gridBoids.update();
        const l = gridBoids.boids.length;
        boidViews.sprites.forEach((sprite, i) => {
            sprite.alpha = i < l ? 1 : 0;
            if (sprite.alpha) {
                const boid = gridBoids.boids[i];
                sprite.tint = boidTemplate.hue;
                sprite.position.set(boid.position.x, boid.position.y);
                sprite.scale.x !== boid.radius && sprite.scale.set(boid.radius, boid.radius);
            }
        });
    };
    display.camera.addChild(boidViews.view);
    return {boids: gridBoids.boids, views: boidViews, update};
}



