import * as React from "react";
import {IToolSelectorProps} from "../ToolSelector";
import {observer} from "mobx-react";
import {hexStringToRgb, rgbArrayToHex} from "src/utils/colorConversions";
import {ToolType} from "src/toolbarUi/model/types";
import {makeSliderInput} from "../../utils/makeSlider";
import makeHueSelector from "../../utils/makeHueSelector";

const transition = "all .3s";

export default observer(
    class ScentOptions extends React.Component<IToolSelectorProps & {selected: ToolType}> {
        public render() {
            const containerStyle = {
                transition,
                borderRadius: "0 0 6px 6px",
                maxHeight: this.props.tools.selected === this.props.selected ? "300px" : "0px", // ugly, find a better solution
                overflow: "hidden",
                background: "white",
                width: "100%",
                display: "flex",
                flexDirection: "column" as "column"
            };
            const inputContainerStyle = {
                transition,
                margin: "8px",
                marginTop: "2px",
                maxWidth: "80px"
            };
            const entryTitleStyle = {fontWeight: 600, textAlign: "center" as "center", fontSize: "12px"};
            const inputStyle = {width: "100%", padding: "0"};
            const props = this.props;
            const scentTool = props.tools.scent;
            const onQuantityChanged = (e: React.ChangeEvent<HTMLInputElement>) => {
                props.actions.scent.changeQuantity(parseInt(e.target.value, 10));
            };
            const onSpreadChanged = (e: React.ChangeEvent<HTMLInputElement>) => {
                props.actions.scent.changeSpread(parseInt(e.target.value, 10));
            };
            const onEvaporateChanged = (e: React.ChangeEvent<HTMLInputElement>) => {
                props.actions.scent.changeEvaporate(1 / parseFloat(e.target.value));
            };
            const onHueChanged = (e: React.ChangeEvent<HTMLInputElement>) => {
                const v = e.target.value.slice(1);
                const rgb = hexStringToRgb(v);
                const hex = rgbArrayToHex(rgb);
                props.actions.scent.changeHue(hex);
            };
            let hueString = scentTool.hue.toString(16);
            while (hueString.length < 6) {
                hueString = "0" + hueString;
            }
            hueString = "#" + hueString;
            return (
                <div style={containerStyle}>
                    {makeSliderInput(scentTool.quantity, 1, 1000, 1, "quantity", onQuantityChanged)}
                    {makeSliderInput(scentTool.spread, 1, 100, 1, "spread", onSpreadChanged)}
                    {makeSliderInput(scentTool.evaporate, 0, 100, 1, "duration", onEvaporateChanged, (v: number) =>
                        Math.round(1 / scentTool.evaporate)
                    )}
                    {makeHueSelector(scentTool.hue, onHueChanged)}
                </div>
            );
        }
    }
);



